<template>
  <v-container
    fluid
  >
    <base-material-card
      class="px-5 py-3 "
      color="indigo"
      icon="mdi-help"
      inline
    >
      <template #after-heading>
        <div class="text-h3 font-weight-light">
          {{ $t('提交问题') }}
        </div>
      </template>
      <v-form
        ref="form"
        v-model="valid"
        :lazy-validation="false"
      >
        <v-container>
          <v-row>
            <v-col
              cols="12"
              md="12"
              sm="6"
            >
              <span class="text-subtitle-1 font-weight-bold">{{ $t('title') }}</span>
            </v-col>
            <v-col
              cols="12"
              md="12"
              sm="6"
            >
              <v-text-field
                v-model="editedItem.title"
                :rules="Utils.ValidateRules.NotEmpty"
                :label="$t('title')"
              />
            </v-col>
          </v-row>
          <v-row>
            <v-col
              cols="12"
              md="12"
              sm="6"
            >
              <span class="text-subtitle-1 font-weight-bold">{{ $t('description') }}</span>
            </v-col>
            <v-col
              cols="12"
              md="12"
              sm="6"
            >
              <v-textarea
                v-model="editedItem.content"
                :rules="Utils.ValidateRules.NotEmpty"
                :label="$t('problem')"
              />
            </v-col>
          </v-row>
          <v-row>
            <v-spacer/>
            <v-btn
              class="mb-2"
              color="primary"
              dark
              @click="summit()"
            >
              {{ $t('提交') }}
            </v-btn>
          </v-row>
        </v-container>
      </v-form>
    </base-material-card>
  </v-container>
</template>

<script>

  import { sendMsg } from '../../components/Utlis/api'
  import Utils from '../../components/Utlis/Utils'

  export default {
    data: function () {
      return ({
        loading: true,
        desserts: [],
        editedIndex: -1,
        valid: true,
        Utils: Utils,
        editedItem: {
          title: '',
          content: '',
        },
        defaultItem: {
          title: '',
          content: '',
        },
      })
    },

    computed: {
      formTitle () {
        return this.editedIndex === -1 ? this.$i18n.t('添加新的分区') : this.$i18n.t('编辑')
      },
    },

    watch: {
      dialog (val) {
        val || this.close()
      },
    },

    created () {
      this.initialize()
    },

    methods: {
      async initialize () {
      },
      summit () {
        if (!this.$refs.form.validate()) {
          return
        }
        Utils.showConfirm('Are you sure?', 'you want to summit', () => {
          sendMsg(this.editedItem.title, this.editedItem.content).then(() => {
            Utils.toast('提交成功')
            this.initialize()
          })
        })
      },
      close () {
        this.dialog = false
        this.editedIndex = -1
      },
    },
  }
</script>
